<template>
	<div class="container">
		<div class="form__wrapper">
			<div class="form__title">
				Change Password
			</div>
			<div class="form__body form__body--sm">
				<div v-if="getErrorMessage" class="alert alert-danger" role="alert">
					{{getErrorMessage}}
				</div>
				<template v-if="!passwordChanged">
					<form @submit.prevent="change">
						<div class="form-row">
							<div class="col-md-12">
								<div class="form-group">
									<label for="password">Password</label>
									<input
										id="password" 
										class="form-control" 
										placeholder="Password" 
										type="password"
										v-model="itemData.password"
										:class="[{'is-invalid': errors.password}]"
										@keydown="clearErrors($event.target.name)">
									<div class="invalid-feedback">
										{{ errors.password }}
									</div>
								</div>
							</div>
						</div>

						<div class="form-row">
							<div class="col-md-12">
								<div class="form-group">
									<label for="c-password">Confirm Password</label>
									<input
										id="c-password" 
										class="form-control" 
										placeholder="Confirm Password" 
										type="password"
										v-model="itemData.confirm_password"
										:class="[{'is-invalid': errors.confirm_password}]"
										@keydown="clearErrors($event.target.name)">
									<div class="invalid-feedback">
										{{ errors.confirm_password }}
									</div>
								</div>
							</div>
						</div>

						<div class="btn__group btn__group--form">
							<button
								v-bind="{'disabled': loading}"
								@click.prevent="change"
								type=submit 
								class="btn__primary btn__primary--main">change Password</button>
						</div>
					</form>
					<div class="form__meta">
						<p>Don't have an account? 
							<router-link
								:to="{name: 'login'}" 
								class="form__link" exact>Sign In</router-link>
						</p>
					</div>
				</template>
				<template v-else>
					<div class="form__text form__text--lg">
						<h4>Your password has been successfully changed! <br> An email has been sent to you.</h4>
					</div>
					<p>Proceed to Sign In
						<router-link
							:to="{name: 'login'}" 
							class="form__link" exact>Sign In</router-link>
					</p>
				</template>
			</div>
		</div>
	</div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import form from '../../mixins/form';
import auth from '../../utils/validations/auth';

export default {
	name: 'ChangePassword',
	mixins: [form],
	data() {
		return {
			itemData: {
				token: ''
			},
			errors: {},
			loading: false,
			passwordChanged: false
		};
	},
	computed: {
		...mapGetters([
			'getStatus',
			'getErrorMessage'
		])
	},
	watch: {
		getStatus(value) {
			if (value === 'success') {
				this.passwordChanged = true;
			}
		}
	},
	methods: {
		...mapActions([
			'changePassword'
		]),
		change() {
			if (this.itemData.password !== this.itemData.confirm_password) {
				this.issues.password = this.issues.confirm_password = 'Errors! Passwords do not match';
			}
			const routePath = this.$route.path;
			const token = routePath.split('/');
			this.itemData.token = token[token.length - 1];
			
			this.validate(this.itemData, auth.passwordChange);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			this.changePassword(this.itemData);
			return true;
		}
	}
};
</script>

<style lang="scss" scoped>
.btn__group--form {
	margin-top: 30px;
	text-align: center;
}
.btn__primary--main {
	width: 230px;
}
.form__text {
	color: $grey;
}
</style>
